.section-about-me {
  @include gridSetting;
  grid-template-rows: 700px;

  &__title {
    margin-top: 128px;
  }

  &__photo {
    z-index: -1;

    @include size(984px);
    transform: translateY(-144px);
  }

  &__video {
    width: 214px;
  }

  &__comments {
    margin-bottom: 84px;
    z-index: 1;

    &__title {
      font-family: var(--second-family);
      color: var(--dark-blue);
      font-size: 26px;
      font-weight: 500;
      text-align: end;
      margin-bottom: 10px;
    }
  }

  &__button {
    transform: translateY(25px);
  }

  @include toDesktop {
    grid-template-rows: 642px;

    &__photo {
      @include size(714px);
      transform: translate(10px, -70px);
    }

    &__title {
      margin-left: 120px;
      max-width: 400px;
    }

    &__comments {
      margin-bottom: 88px;
    }
  }

  @include toLabtop {
    grid-template-rows: 580px;

    &__photo {
      @include size(494px);
      transform: translate(10px, -40px);
    }

    &__title {
      margin-top: 90px;
      margin-left: 0;
      max-width: 400px;
    }

    &__video {
      width: 160px;
      height: 145px;
    }

    &__comments {
      margin-bottom: 94px;
      max-width: 300px;
    }

    &__button {
      transform: translate(50px, -20px);
    }
  }

  @include toPhone {
    grid-template-rows: auto;
    margin-bottom: -40px;

    &__photo {
      @include size(488px);
      transform: none;
      margin: -100px -100%;
    }

    &__title {
      margin-top: 76px;
    }

    &__video {
      text-align: center;
      width: 100%;
      height: 100%;
    }

    &__comments {
      margin-bottom: 30px;
      max-width: 100%;

      &__title {
        font-size: 24px;
        margin-bottom: 16px;
        text-align: center;
      }
    }

    &__button {
      transform: none;
      margin: 18px;
    }
  }
}
