.card-visitca {
  $origin: &;
  display: flex;
  width: 100%;
  max-width: max-content;
  justify-content: space-between;
  gap: 35px;

  &__image {
    height: 142px;
    width: 142px;
    min-width: 142px;
    object-fit: cover;
    border-radius: 100%;
    overflow: hidden;
  }

  &__container {
    display: flex;
    flex-direction: column;
    width: 324px;
    gap: 20px;
  }

  &__title {
    font-family: var(--second-family);
    font-weight: 500;
    font-size: 28px;
    line-height: 100%;
    color: var(--dark-blue);
  }

  &__text {
    line-height: 120%;
    color: var(--text);
  }

  &--top-right {
    flex-direction: column;
    align-items: flex-end;
    text-align: end;
    gap: 20px;

    #{$origin}__container {
      gap: 18px;
    }

    #{$origin}__image {
      transform: scaleX(-1);
    }
  }

  &--top-left {
    flex-direction: column;
    align-items: flex-start;
    text-align: start;
    gap: 20px;

    #{$origin}__container {
      gap: 18px;
    }

    #{$origin}__image {
      transform: scaleX(1);
    }
  }
}
