.section-privilege {
  @include toPhone {
    margin-top: -250px;
    margin-bottom: -230px;
  }

  &__title-block {
    flex-direction: column;
    justify-content: center;
    align-items: center;

    @include toLabtop {
      margin-bottom: -200px;
    }

    @include toPhone {
      margin: 0;
    }
  }

  &__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    margin-top: 34px;
    gap: 20px;

    #{"&"} > div {
      place-self: center;
    }

    @include toLabtop {
      grid-template-columns: repeat(2, 1fr);
    }
    
    @include toPhone {
      display: flex;
      flex-direction: column;
      margin-top: -40px;
    }
  }

  &__block-1 {
    transform: translateY(-268px) rotate(-15deg);

    @include toLabtop {
      transform: none;
    }
    
    @include toPhone {
      transform: scale(.8) rotate(-15deg) translateX(-20%);
      margin-bottom: -120px;
    }
  }

  &__block-2 {
    @include toPhone {
      transform: scale(.8) translateX(20%);
      z-index: 2;
      margin-bottom: -150px;
    }
  }

  &__block-3 {
    transform: translateY(-268px) rotate(15deg) ;

    @include toLabtop {
      transform: none;
    }

    @include toPhone {
      transform: scale(.8) rotate(-15deg) translateX(-20%);
      margin-bottom: -90px;
    }
  }

  &__block-4 {
    @include toPhone {
      margin-top: 20px;
    }
  }

  &__block-5 {
    transform: translateY(-333px);

    @include toDesktop {
      transform: translateY(100px);
    }

    @include toLabtop {
      transform: translateY(20px) rotate(-15deg);
    }

    @include toPhone {
      margin-top: -50px;
      margin-bottom: -100px;
    }
  }

  &__block-6 {
    @include toPhone {
      margin-bottom: -50px;
    }
  }

  &__block-7 {
    rotate: -15deg;

    @include toDesktop {
      transform: translateY(-100px);
    }

    @include toLabtop {
      transform: none; 
      rotate: 0deg;
    }

    @include toPhone {
      margin: auto;
    }
  }

  &__block-mini {
    transform: translateY(-86px); 
    display: flex; 
    flex-direction: column; 
    align-items: end;

    @include toLabtop {
      transform: none;
    }

    @include toPhone {
      justify-content: end;
      align-items: end;
    }
  }

  &__container-2 {
    row-gap: 174px;

    @include toLabtop {
      row-gap: 20px;
    }
  }

  &__button {
    @include toDesktop {
      transform: translateY(120px);
    }

    @include toLabtop {
      transform: none;
    }

    @include toPhone {
      transform: translateY(-90px);
    }
  }
}
