.section-magazine {
  row-gap: 60px;

  &__container {
    display: grid; 
    grid-template-columns: 67% 33%;

    @include toLabtop {
      grid-template-columns: 50% 50%;
    }

    @include toPhone {
      display: flex;
      flex-direction: column-reverse;
      gap: 20px
    }
  }

  @include toTablet {
    row-gap: 38px;
  }
}
