.card-tariffs {
  $origin: &;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  $gap: 23px;
  background-color: var(--white);
  border-radius: 30px;
  border: 1px solid var(--light-blue);
  padding-bottom: 40px;
  overflow: hidden;

  &__head {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    position: relative;
    text-align: center;
    line-height: 100%;
    margin-bottom: $gap;
    padding-top: 48px;
    padding-bottom: 38px;
    font-family: var(--second-family);
  }

  &__body {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    gap: 40px;
  }

  &__bg {
    @include size(494px);

    position: absolute;
    border-radius: 100%;
    background-color: #ffe7f1;
    bottom: 0;
  }

  &__title {
    position: relative;

    font-size: 36px;
    color: var(--dark-blue);

    margin-bottom: 35px;
  }

  &__price {
    position: relative;
    font-weight: 700;
    font-size: 46px;
    color: var(--dark-blue);
    margin-bottom: 25px;
  }

  &__old-price {
    position: relative;
    font-size: 24px;
    text-decoration: line-through;
    color: var(--text);
    opacity: 0.5;
  }

  &__list {
    padding-inline: 25px;
    display: flex;
    flex-direction: column;
    gap: $gap;
  }

  &__item {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 120%;
    letter-spacing: 0.02em;
    color: var(--text);

    padding-bottom: $gap;

    border-bottom: 1px solid #e0e6ed;
  }

  &__button {
    padding-inline: 68px;
    bottom: 0;
  }

  &--pro {
    #{$origin}__bg {
      background-color: #ebe7ff;
    }
  }

  &--best {
    box-shadow: 0 0 55px 0 rgb(150 182 220 / 80%);
    background-color: var(--light-blue);

    #{$origin}__head {
      padding-top: 45px;
      padding-bottom: 50px;

      &::before {
        content: " i";
        display: flex;
        align-items: end;

        position: absolute;
        background-color: var(--light-blue);
        border-radius: 28px;
        width: 180px;
        height: 110px;
        padding: 0 49px 7px 0;
        top: -28%;
        left: -13%;
        z-index: 1;

        font-family: var(--font-family);
        font-weight: 500;
        font-size: 15px;
        line-height: 100%;
        text-align: center;
        color: var(--dark-blue);

        transform: rotate(-45deg);
      }
    }

    #{$origin}__bg {
      background-color: #ffd1e4;
    }

    #{$origin}__title {
      font-size: 46px;
    }

    #{$origin}__price {
      font-size: 52px;
    }

    #{$origin}__item {
      color: var(--white);
      border-color: var(--white);
    }
  }
}

.section-timer {
  display: flex;
  flex-direction: column;
  align-items: center;

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 25px;
    line-height: 120%;
    text-align: center;
    color: var(--dark-blue);

    margin-bottom: 30px;
  }
}

.date-timer {
  display: flex;
  justify-content: space-around;

  border-radius: 50px;
  width: 100%;
  max-width: 820px;
  height: 176px;
  padding-block: 32px 24px;
  padding-inline: 50px;

  box-shadow: 0 2px 10px 0 rgb(39 76 91 / 7%);
  background: linear-gradient(180deg, rgb(150 182 220 / 50%) 0%, #fff 100%);

  &__line {
    height: 100%;
    width: 1px;

    background: var(--gray);
    opacity: 0.3;
  }

  &__item {
    width: 100px;
    text-align: center;
    margin-top: 10px;
  }

  &__count {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 60px;
    line-height: 100%;
    color: var(--dark-blue);
    margin-bottom: 20px;
  }

  &__date-type {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 22px;
    line-height: 100%;
    text-align: center;
    color: #2e383b;
  }

  @include toLabtop {
    height: 160px;
    border-radius: 30px;

    &__item {
      margin-top: 4px;
    }

    &__count { font-size: 44px }
    &__date-type { font-size: 20px }
  }

  @include toPhone {
    height: 96px;
    padding-block: 16px 12px;
    padding-inline: 8px;
    border-radius: 15px;

    &__item {
      width: 70px;
      margin-top: 0;
    }

    &__count { font-size: 26px }
    &__date-type { font-size: 16px }
  }
}

