.module-tree {
  @include flex-center;
  flex-direction: column;

  &__line-vertical {
    height: 150px;
    width: 1px;
    background-color: var(--light-blue);
  }
  
  &__join {
    @include flex-center;
    position: relative;
    width: max-content;
  }
  
  &__side {
    @include flex-center;
    position: absolute;
    width: max-content;

    left: 100%;
    flex-direction: row;
    
    &--reverse {
      left: initial;
      right: 100%;
      flex-direction: row-reverse;
    }

    &--bottom {
      top: -100%;
    }
  }
  
  &__number {
    @include size(68px);
    @include shadow;
    background-color: var(--white);
    border-radius: 100%;
    line-height: 130%;
    z-index: 1;

    color: var(--light-blue);
    font-family: var(--second-family);
    font-weight: 500;
    font-size: 45px;
    letter-spacing: 0.01em;
    text-align: center;
  }
  
  &__line-horizontal {
    height: 1px;
    width: 140px;
    background-color: var(--light-blue);
  }
  
  &__point {
    @include size(16px);
    border-radius: 100%;
    background-color: var(--light-blue);
  }
  
  &__title {
    padding-inline: 20px;
  }
}
