.section-instruction {
  @include flex-center;
  flex-direction: column;

  position: relative;
  margin-top: -60px;
  margin-bottom: 100px;
  
  &__header {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    margin-bottom: 70px;
  }

  &__title {
    width: 600px;
    text-align: center;
  }

  &__body {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 60px;
  }

  &::before {
    content: "";

    @include size(140vw);
    position: absolute;
    top: -48px;
    display: block;
    border-radius: 100%;
    background: var(--white);
    z-index: -3;
  }
}
