  &__bg {
    @include no-event;
    position: absolute;
    max-width: 2000px;
    top: -50%;
    left: 50%;
    z-index: -1;
  }

  &__image {
    display: block;
    width: 142px;
    height: 142px;
    object-fit: cover;
    border-radius: 100%;
    margin-bottom: 50px;
    z-index: 1;
  }

  &__text {
    position: relative;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 19px;
    line-height: 87%;
    letter-spacing: 0.02em;
    text-align: center;
    color: var(--text);
    z-index: 1;

    margin-bottom: 18px;

    &::after {
      content: "";
      top: 0;
      left: 0;
      position: absolute;
      transform: translate(-75%, -100%);

      @include size(16px);
      background-color: var(--light-blue);
      border-radius: 100%;
    }
  }

  &__title {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 45px;
    line-height: 100%;
    letter-spacing: 0.02em;
    text-align: center;
    color: var(--dark-blue);
    width: 616px;
    z-index: 1;

    @include toLabtop {
      font-size: 36px;
      width: 516px;
    }
  }

  @include toPhone {
    width: 100%;
    padding: 0;

    &__bg {
      max-width: 1100px;
      top: -50%;
      left: 50%;
      transform: translate(-40.5%, 8.5%);
      z-index: -1;
    }

    &__image {
      margin-bottom: 14px;
    }

    &__text {
      text-align: start;
      font-size: 16px;
      align-self: flex-start;
    }

    &__title {
      text-align: start;
      font-size: 25px;
      width: 100%;
    }
  }
}
