.header {
  @include gridSetting;

  $origin: &;

  align-items: center;
  padding: 0 50px;

  border-radius: 0 0 30px 30px;
  box-shadow: 0 4px 10px 0 #96b6dc4d;
  background: #ffffffe6;

  -webkit-backdrop-filter: blur(16px);
  backdrop-filter: blur(16px);

  &__logo {
    grid-column: 1/1;
    padding-right: 25px;
  }

  &__container {
    grid-column: 2/5;
    display: flex;
    align-items: center;
    gap: clamp(100px, 3vw, 10%);
  }

  &__navigation {
    width: 45%;
  }

  &__button {
    grid-column: 5/7;
    justify-self: end;
    max-width: 214px;
    width: 100%;
  }

  &__slide-menu {
    display: none;
  }

  @include toDesktop {
    padding: 0 20px;
    display: flex;
    justify-content: space-between;

    &__logo {
      width: 184px;
      padding: 0;
    }

    &__container {
      justify-content: space-around;
      gap: none;
    }
  }

  @include toLabtop {
    height: 70px;

    &__navigation {
      display: none;
    }

    &__slide-menu {
      display: block;
    }
  }

  @include toPhablet {
    &__link {
      display: none;
    }

    &__button {
      display: none;
    }
  }

  @include toPhone {
    padding: 0 15px;

    &__logo {
      width: 130px;
    }
  }
}
