.section-contact-me {
  &__decor {
    @include toLabtop {
      @include flex-center;
      flex-direction: column;
      align-items: end;
    }

    @include toPhone {
      align-items: center;
    }
  }
}

.contacts {
  $origin: &;
  text-align: end;
  margin-top: 23px;

  &__name {
    font-family: var(--second-family);
    font-weight: 500;
    font-size: 26px;
    line-height: 100%;
    letter-spacing: 0.01em;
    color: var(--dark-blue);
    margin-bottom: 66px;
    order: 1;
  }

  &__links {
    display: flex;
    flex-direction: column-reverse;
    gap: 10px;
  }

  &__link {
    display: flex;
    justify-content: end;
    gap: 6px;
    margin-bottom: 10px;

    &:hover {
      text-decoration: underline;
    }

    & > *:link {
      font-size: clm_px(14, map-get($breakpoints, tablet), 20);
      text-transform: lowercase;
      color: var(--light-blue);
    }
  }

  @include toLabtop {
    #{$origin}__name {
      margin-bottom: 22px;
    }

    #{$origin}__links {
      flex-direction: column;
    }
  }

  @include toPhone {
    #{$origin}__name {
      font-weight: 700;
      font-size: 20px;
      margin-bottom: 22px;
    }

    #{$origin}__links {
      flex-direction: column-reverse;
    }

    #{$origin}__link {
      & > *:link {
        font-size: clm_px(16, map-get($breakpoints, phone), 20);
      }
    }
  }
}
